import { Document } from "api/collections";
import { CollectionType, FieldType, updateCollectionType } from "api/collectionTypes";
import { getCollection, GetCollectionOptions, deleteDocument } from "api/collections";
import FilesCell from "./FilesCell";
import IconButton from "components/IconButton";
import EditRounded from "@material-ui/icons/EditOutlined";
import DeleteRounded from "@material-ui/icons/DeleteOutlineOutlined";
import InViewFetcher from "components/InViewFetcher";
import SettingsRounded from "@material-ui/icons/SettingsOutlined";
import Checkbox from "@material-ui/core/Checkbox";
import React from "react";
import ExpandMoreRounded from "@material-ui/icons/ExpandMoreRounded";
import { useHistory } from "react-router-dom";
import { confirm } from "components/Confirm";
import store from "store";
import AddFilter from "./AddFilter";
import { createIndex } from "./CreateIndex";
import { useNotify } from "components/NotificationsProvider";
import Button from "components/Button";
import { configureView } from "components/TableViewConfig";

export interface TableProps {
  collectionType: CollectionType;
  onSelect?: (a: Document[]) => void;
  blackList?: string[];
  singleSelect?: boolean;
}

const Table: React.FC<TableProps> = ({
  collectionType,
  onSelect,
  blackList = [],
  singleSelect = false,
}) => {
  const _configureView = async () => {
    configureView({
      fields: collectionType.fields,
      displayDocIdOnTable: collectionType.displayDocIdOnTable,
      callback: onConfigureView,
    });
  };

  const history = useHistory();
  const notify = useNotify();
  type filterType = { fieldId: string; operator: string; value: string };
  const [filters, setFilters] = React.useState<filterType[]>([]);

  const [docs, setDocs] = React.useState<Document[]>([]);

  const fields = collectionType.fields.filter((field) => field.displayOnTable);
  const [selected, setSelected] = React.useState<number[]>([]);
  const [orderBy, setOrderBy] = React.useState<{ fieldId: string; direction: "asc" | "desc" }>();

  React.useEffect(() => {
    setDocs([]);
  }, [orderBy, filters]);

  const onEdit = (doc: Document) => {
    history.push(`/collections/${collectionType.id}/edit/${doc.docId}`);
  };

  const onDelete = async (doc: Document) => {
    if (await confirm({ confirmation: "Do you really want to delete the document?" })) {
      deleteDocument(collectionType.id, doc.docId);
      setDocs((prev) => {
        let arr = [...prev];
        const index = arr.findIndex((x) => x.docId === doc.docId);
        arr.splice(index, 1);
        return arr;
      });
    }
  };

  const onDeleteSelected = async () => {
    if (
      await confirm({ confirmation: `Do you really want to delete ${selected.length} documents?` })
    ) {
      selected.forEach((x) => {
        deleteDocument(collectionType.id, docs[x].docId);
      });
      setDocs((prev) => prev.filter((_, i) => !selected.includes(i)));
      setSelected([]);
    }
  };

  const onConfigureView = ({
    fields,
    displayDocIdOnTable,
  }: {
    fields: FieldType[];
    displayDocIdOnTable?: boolean;
  }) => {
    const display = displayDocIdOnTable != null ? displayDocIdOnTable : true;
    updateCollectionType(collectionType.docId, {
      ...collectionType,
      fields,
      displayDocIdOnTable: display,
    });

    store.dispatch({
      type: "UPDATE_COLLECTION",
      docId: collectionType.docId,
      payload: { fields, displayDocIdOnTable: display },
    });
  };

  const onOrder = (field: FieldType) =>
    orderBy?.fieldId !== field.id
      ? () => {
          setOrderBy({ fieldId: field.id, direction: "asc" });
        }
      : orderBy?.direction !== "asc"
      ? () => setOrderBy(undefined)
      : () => setOrderBy({ fieldId: field.id, direction: "desc" });

  return (
    <div className="overflow-auto">
      <div className="flex justify-between mb-3">
        <AddFilter
          collectionType={collectionType}
          onValue={(val: filterType) => setFilters((prev) => [val, ...prev])}
        ></AddFilter>
        {!onSelect ? (
          <Button
            onClick={onDeleteSelected}
            disabled={selected.length === 0}
            className={`${
              selected.length === 0
                ? "bg-gray-300 text-gray-500 cursor-default"
                : "bg-blue-300 hover:bg-blue-400 text-white"
            } `}
          >
            Delete
          </Button>
        ) : (
          <Button
            onClick={() => onSelect(selected.map((selInd) => docs[selInd]))}
            className={`${
              selected.length === 0
                ? "bg-gray-300 text-gray-500 cursor-default"
                : "bg-blue-300 hover:bg-blue-400 text-white"
            } `}
          >
            Select
          </Button>
        )}
      </div>
      <div className="flex flex-wrap text-sm">
        {filters.map((f, i) => (
          <div
            key={`f-${i}`}
            className="text-blue-400 rounded h-34px leading-34px pl-5 pr-3 flex mr-3 mb-3 relative"
          >
            <div className="absolute left-0 top-0 w-full h-full bg-blue-400 opacity-10 rounded pointer-events-none"></div>
            {f.fieldId} {f.operator} {f.value}{" "}
            <span
              onClick={() =>
                setFilters((prev) => {
                  let arr = [...prev];
                  arr.splice(i, 1);
                  return arr;
                })
              }
              className="text-xs leading-34px ml-3 cursor-pointer"
            >
              
            </span>
          </div>
        ))}
      </div>
      <table className="w-full text-center" style={{ minWidth: (fields.length + 2) * 142 }}>
        <thead>
          <tr className="bg-gray-300 h-48px rounded">
            <th className="w-14">
              {singleSelect ? null : (
                <Checkbox
                  size="small"
                  classes={{ checked: "text-blue-400" }}
                  checked={selected.length === docs.length}
                  onChange={(e) => setSelected(e.target.checked ? docs.map((_, i) => i) : [])}
                ></Checkbox>
              )}
            </th>

            {collectionType.displayDocIdOnTable ? <th className="font-medium">docId</th> : null}

            {fields.map((field, i) => (
              <th className="font-medium" key={`th-${i}`}>
                <div className="flex items-center justify-center">
                  <div className="w-5"></div>
                  <div className="cursor-pointer select-none" onClick={onOrder(field)}>
                    {field.id}
                  </div>
                  <div className="w-5">
                    {orderBy?.fieldId === field.id ? (
                      <ExpandMoreRounded
                        fontSize="small"
                        className={`${orderBy?.direction === "desc" ? "transform rotate-180" : ""}`}
                      ></ExpandMoreRounded>
                    ) : null}
                  </div>
                </div>
              </th>
            ))}
            <th className="w-20">
              <div className="flex justify-end px-2">
                <IconButton
                  variant="transparent"
                  onClick={() => _configureView()}
                  className="cursor-pointer"
                >
                  <SettingsRounded fontSize="small"></SettingsRounded>
                </IconButton>
              </div>
            </th>
          </tr>
        </thead>
        <tbody>
          {docs.map((doc, i) =>
            blackList.includes(doc.docId) ? null : (
              <tr
                onClick={() => onEdit(doc)}
                key={`row-${i}`}
                className="h-48px hover:bg-gray-300 cursor-pointer"
              >
                <td className="w-14">
                  <Checkbox
                    size="small"
                    classes={{ checked: "text-blue-400" }}
                    checked={selected.includes(i)}
                    onClick={(e) => e.stopPropagation()}
                    onChange={(e) => {
                      if (singleSelect) {
                        setSelected([i]);
                      } else {
                        setSelected((prev) => {
                          if (e.target.checked) {
                            return [...prev, i];
                          }
                          let arr = [...prev];
                          const index = arr.findIndex((s) => s === i);
                          arr.splice(index, 1);
                          return arr;
                        });
                      }
                    }}
                  ></Checkbox>
                </td>
                {collectionType.displayDocIdOnTable ? <td>{doc.docId}</td> : null}
                {fields.map((field, j) => (
                  <td key={`row-${i}-col-${j}`} className="px-2">
                    {doc[field.id] ? (
                      field.type === "media" ? (
                        field.mediaSingle ? (
                          <FilesCell file={doc[field.id]}></FilesCell>
                        ) : (
                          <FilesCell files={doc[field.id]} />
                        )
                      ) : (
                        <div className="line-clamp-1">
                          {doc[field.id].toString().substring(0, 40)}
                        </div>
                      )
                    ) : null}
                  </td>
                ))}
                <td className="w-20">
                  {onSelect == null ? (
                    <div className="flex justify-end px-2">
                      <IconButton
                        variant="transparent"
                        onClick={(e: MouseEvent) => {
                          e.stopPropagation();
                          onEdit(doc);
                        }}
                        className="cursor-pointer"
                      >
                        <EditRounded fontSize="small"></EditRounded>
                      </IconButton>
                      <IconButton
                        variant="transparent"
                        className="cursor-pointer"
                        onClick={(e: MouseEvent) => {
                          e.stopPropagation();
                          onDelete(doc);
                        }}
                      >
                        <DeleteRounded fontSize="small"></DeleteRounded>
                      </IconButton>
                    </div>
                  ) : null}
                </td>
              </tr>
            )
          )}
          <tr>
            <td colSpan={100}>
              <InViewFetcher
                key={(orderBy ? orderBy.fieldId + orderBy.direction : "no-order") + filters.length}
                limit={10}
                onValue={(vals) => setDocs((prev) => [...prev, ...vals])}
                onError={(message: string) => {
                  if (message.includes("FAILED_PRECONDITION")) {
                    createIndex(message.split("it here:")[1]);
                  } else {
                    notify(message, { variant: "error" });
                  }
                }}
                fetcher={() => {
                  let options: GetCollectionOptions = { collectionId: collectionType.id };

                  options.where = "";
                  options.orderBy = "";

                  filters.forEach((f, i) => {
                    options.where +=
                      (i > 0 ? ";" : "") + f.fieldId + "," + f.operator + "," + f.value;
                    if (orderBy && orderBy.fieldId !== f.fieldId) {
                      options.orderBy += (options.orderBy ? ";" : "") + f.fieldId;
                    }
                  });

                  if (orderBy) {
                    options.orderBy +=
                      (options.orderBy ? ";" : "") + `${orderBy.fieldId},${orderBy.direction}`;
                  }
                  if (!orderBy && !filters.length) {
                    options.orderBy = "created_at,asc";
                  }

                  if (docs.length) {
                    options.startAfter = docs[docs.length - 1][orderBy ? orderBy.fieldId : "docId"];
                  }
                  options.populateRef = false;
                  return getCollection(options);
                }}
              ></InViewFetcher>
            </td>
          </tr>
        </tbody>
        {docs.length < 8 ? null : (
          <tfoot>
            <tr className="bg-gray-300 h-48px rounded">
              <th className="w-14">
                {singleSelect ? null : (
                  <Checkbox
                    size="small"
                    classes={{ checked: "text-blue-400" }}
                    checked={selected.length === docs.length}
                    onChange={(e) => setSelected(e.target.checked ? docs.map((_, i) => i) : [])}
                  ></Checkbox>
                )}
              </th>

              {collectionType.displayDocIdOnTable ? <th className="font-medium">docId</th> : null}
              {fields.map((field, i) => (
                <th className="font-medium" key={`th-${i}`}>
                  <div className="flex items-center justify-center">
                    <div className="w-5"></div>
                    <div className="cursor-pointer select-none" onClick={onOrder(field)}>
                      {field.id}
                    </div>
                    <div className="w-5">
                      {orderBy?.fieldId === field.id ? (
                        <ExpandMoreRounded
                          fontSize="small"
                          className={`${
                            orderBy?.direction === "desc" ? "transform rotate-180" : ""
                          }`}
                        ></ExpandMoreRounded>
                      ) : null}
                    </div>
                  </div>
                </th>
              ))}
              <th className="w-20">
                <div className="flex justify-end px-2">
                  <IconButton
                    variant="transparent"
                    onClick={() => _configureView()}
                    className="cursor-pointer"
                  >
                    <SettingsRounded fontSize="small"></SettingsRounded>
                  </IconButton>
                </div>
              </th>
            </tr>
          </tfoot>
        )}
      </table>
    </div>
  );
};

export default Table;
